* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  text-align: center;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.canvasStage > div {
  background-color: rgb(41, 4, 48);
  margin: 0;
}

button:active,
input:active {
  border: 0 !important;
  outline: 0 !important;
}

button:focus,
input:focus {
  border: 0 !important;
  outline: 0 !important;
}



